import Joi from '@hapi/joi';

import { Platform } from '../../constants';

export interface AndroidKeystore {
  dataBase64: string;
  keystorePassword: string;
  keyAlias: string;
  keyPassword: string;
}

const AndroidKeystoreSchema = Joi.object({
  dataBase64: Joi.string().required(),
  keystorePassword: Joi.string().required(),
  keyAlias: Joi.string().required(),
  keyPassword: Joi.string().required(),
});


interface BaseAndroidJob {
  platform: Platform.Android;
  projectUrl: string;
  secrets?: {
    keystore: AndroidKeystore;
  }
}

const BaseAndroidJobSchema = Joi.object({
  platform: Joi.string().valid(Platform.Android).required(),
  projectUrl: Joi.string().uri().required(),
  secrets: Joi.object({
    keystore: AndroidKeystoreSchema.required(),
  }),
});

export { BaseAndroidJob, BaseAndroidJobSchema };
